.list-card {
  padding: 0;
  width: 100%;
}
  
.list-card > ul {
  --gap: 1.4rem;
  margin: calc(-1 * var(--gap)) 0 0 calc(-1 * var(--gap));
  width: calc(100% + var(--gap));
  display: inline-flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.list-card > ul > * {
  padding: var(--gap) 0 0 var(--gap);
  width: 25%;
  min-width: 16rem;
  justify-content: center;
  margin: 1rem 0;
}

.card {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #39404c;
  width: 15rem;
  height: 15rem;
  border-radius: 1em;
  margin: 0 auto;
}

.card:hover {
  box-shadow: 0 0 0.7em 0.2em lightblue;
}

.card img {
  width: 75%;
  height: 75%;
  object-fit: cover;
  border-radius: 1em;
  transition: height 0.3s, width 0.3s;
}

.card:hover img {
  object-fit: cover;
  width: 80%;
  height: 80%;
}

/*Ipad ngang(1024 x 768)*/
@media screen and (max-width: 1024px){
  .list-card > ul > * {
      width: 33.33333334%;
      min-width: 15rem;
  }
  
  .card {
      width: 14rem;
      height: 14rem;
  }
}
/*Ipad dọc(768 x 1024)*/
@media screen and (max-width: 768px){
  .list-card > ul > * {
      width: 50%;
      min-width: 14rem;
  }
  
  .card {
      width: 13rem;
      height: 13rem;
  }
  
}
/*Tablet nhỏ(480 x 640)*/
@media screen and (max-width: 480px){
  .list-card > ul > * {
      width: 100%;
      min-width: 15rem;
  }
  
  .card {
      width: 14rem;
      height: 14rem;
  }
  
}
