.banner {
    display: flex;
    flex-direction: row;
    max-width: 100%;
    width: 100%;
    border-radius: 1em;
}

img {
    object-fit: cover;
}

.banner-list {
    height: 49.5em;
    margin: 0;
    display: flex;
    flex-direction: column;
    min-width: 10em;
    overflow-x: hidden;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

.banner-list::-webkit-scrollbar {
    display: none;
}

.banner-list li {
    padding: 1em;
    min-height: 10em;
    width: 10em;
}

.banner-list li button {
    position: relative;
    align-content: center;
    width: 100%;
    height: 100%;
    padding: 0;
}

.banner-list-img {
    border-radius: 10em;
    width: 90%;
    height: 90%;
}

.banner-list-img:hover {
    border-radius: 10em;
    width: 100%;
    height: 100%;
}

.banner-content {
    width: 100%;
    flex-grow: 1;
    flex-shrink: 1;
}

.banner-content img {
    width: 100% !important;
    height: 49.5em;
    border-radius: 1em;
}

.show-list {
    height: 49.5em;
    flex-direction: row;
    flex-wrap: wrap;
    --gap: 1.5em;
    margin: calc(-1 * var(--gap)) 0 0 calc(-1 * var(--gap));
    min-width: calc(100% + var(--gap)) !important;
    width: 100%;
}

.show-list > li {
    padding: var(--gap) 0 0 var(--gap) !important;
    width: 25% !important;
    min-width: 15rem;
    justify-content: center;
}

.show-list > li > * {
    margin-right: -1em;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 15em !important;
    width: 15em !important;
    margin: 0 auto;
}